body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.breed-select-container {
  display: none;
}

h1 {
  text-align: center;
  padding: 20px 0;
}

img {
  width: 350px;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.select-container {
  margin-bottom: 20px;
}

.loader {
  text-align: center;
  margin-bottom: 20px;
  display: none;
}

.error {
  text-align: center;
  color: red;
  margin-bottom: 20px;
  display: none;
}

.cat-info {
  text-align: center;
  display: none;
}

.loader-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 0 auto 10px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

